<template>
  <div class="component">
    <div class="box">
      <CreateDialog :payload="createPayload" />
      <DeleteDialog :payload="deletePayload" />
      <UpdateDialog :payload="updatePayload" />

      <!--  -->
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">{{ masterName }}</div>
          <v-select
            v-if="categoryOption !== undefined"
            @change="categoryChange"
            v-model="category"
            :items="categories"
            :item-text="(item) => item[categoryOption.textKey]"
            :item-value="(item) => item[categoryOption.valueKey]"
            menu-props="auto"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
            style="width: 250px"
          >
          </v-select>

          <v-btn
            color="primary"
            dark
            @click="
              createItem(masterName, masterApi, masterTableList, masterHeaders)
            "
          >
            New Item
          </v-btn>
          <div class="spacer"></div>

          <v-text-field
            v-model="masterTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
          <v-btn
            v-if="formDownloadApi"
            color="black"
            dark
            @click="download(formDownloadApi)"
          >
            form Download
          </v-btn>
          <v-btn
            v-if="csvDownloadApi"
            color="black"
            dark
            @click="download(csvDownloadApi)"
          >
            csv Download
          </v-btn>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :single-select="true"
            :search="masterTableSearch"
            :headers="masterHeaders"
            :items="masterTableList"
            :items-per-page="-1"
            :item-key="tableKey"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="
                  updateItem(
                    masterName,
                    masterApi,
                    masterTableList,
                    masterHeaders,
                    item
                  )
                "
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(masterApi, masterTableList, item)"
              >
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { getRequest, fileDownloadRequest } from "@/services/request.js";
import DeleteDialog from "@/dialogs/DeleteItemDialog.vue";
import CreateDialog from "@/dialogs/CreateItemDialog.vue";
import UpdateDialog from "@/dialogs/UpdatItemDialog.vue";
// import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "SelectComponent",
  components: {
    CreateDialog,
    DeleteDialog,
    UpdateDialog,
  },
  props: {
    obj: Object,
  },
  setup(props) {
    const {
      csvDownloadApi,
      formDownloadApi,

      categoryOption,
      masterName,
      masterApi,
      masterHeaders,
    } = props.obj;

    const category = ref("");
    const categories = ref(null);

    const download = (api) => {
      fileDownloadRequest(
        api,
        {
          parent_key: category.value,
        },
        (filename, blob) => {
          const url = URL.createObjectURL(blob);
          const a = document.createElement("a");
          a.href = url;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          window.URL.revokeObjectURL(url);
        }
      );
    };

    const tableKey = ref(masterHeaders[0].value);

    masterHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });

    const masterTableList = ref([]);
    const masterTableSearch = ref("");

    const init = () => {
      const [fileName, apiName] = masterApi;

      if (category.value === "") {
        getRequest([fileName, apiName + "s"], null, (masterList) => {
          masterTableList.value = masterList || [];
        });
      } else {
        getRequest(
          [fileName, apiName + "s"],
          {
            filterKey: category.value,
          },
          (masterList) => {
            masterTableList.value = masterList || [];
          }
        );
      }
    };

    if (!categoryOption) {
      init();
    } else {
      const [fileName, apiName] = categoryOption.api;
      getRequest([fileName, apiName], null, (list) => {
        category.value = list[0][categoryOption.valueKey];
        categories.value = list;
        init();
      });
    }

    const categoryChange = (value) => {
      category.value = value;
      init();
    };

    //
    const createPayload = ref(null);
    const updatePayload = ref(null);
    const deletePayload = ref(null);

    const createItem = (title, api, list, fields) => {
      const filter = {};
      if (categoryOption) {
        filter[categoryOption.modelKey] = category.value;
      }

      const parentItem = {};
      parentItem.saveType = "new";
      parentItem[tableKey.value] = null;

      createPayload.value = {
        title,
        api,
        list,
        fields,
        parentItem,
        filter,
      };
    };
    const updateItem = (title, api, list, fields, item) => {
      const filter = {};
      if (categoryOption) {
        filter[categoryOption.modelKey] = category.value;
      }
      const parentItem = {};

      parentItem.saveType = "update";
      parentItem[tableKey.value] = item[tableKey.value];

      updatePayload.value = {
        title,
        api,
        list,
        fields,
        item,
        parentItem,
        filter,
      };
    };
    const deleteItem = (api, list, item) => {
      deletePayload.value = {
        list,
        item,
        api,
      };
    };

    return {
      download,
      csvDownloadApi,
      formDownloadApi,

      tableKey,

      categoryChange,
      categoryOption,
      category,
      categories,

      masterName,
      masterApi,
      masterHeaders,
      masterTableList,
      masterTableSearch,

      createItem,
      updateItem,
      deleteItem,

      createPayload,
      updatePayload,
      deletePayload,
    };
  },
};
</script>
