<template>
  <div class="component">
    <div class="box">
      <CreateDialog :payload="createPayload" />
      <DeleteDialog :payload="deletePayload" />
      <UpdateDialog :payload="updatePayload" />

      <!--  -->
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">{{ masterName }}</div>
          <v-select
            v-if="categoryOption !== undefined"
            @change="categoryChange"
            v-model="category"
            :items="categories"
            :item-text="(item) => item[categoryOption.textKey]"
            :item-value="(item) => item[categoryOption.valueKey]"
            menu-props="auto"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
            style="width: 200px"
          >
          </v-select>
          <v-btn
            v-if="masterNewItem !== false"
            color="primary"
            dark
            @click="
              createItem(
                'master',
                masterName,
                masterTableKey,
                masterApi,
                masterTableList,
                masterHeaders
              )
            "
          >
            New Item
          </v-btn>
          <div class="spacer"></div>
          <v-text-field
            v-model="masterTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            class="selectTable"
            :single-select="true"
            @click:row="selectTableItem"
            :search="masterTableSearch"
            :headers="masterHeaders"
            :items="masterTableList"
            :items-per-page="-1"
            :item-key="masterTableKey"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="
                  updateItem(
                    'master',
                    masterName,
                    masterTableKey,
                    masterApi,
                    masterTableList,
                    masterHeaders,
                    item
                  )
                "
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(masterApi, masterTableList, item)"
              >
                mdi-delete
              </v-icon>
            </template>
            <template v-slot:[`item.icon`]="{}">
              <v-icon small class="mr-2"> mdi-arrow-right-bold </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
      <!--  -->
      <div class="col" style="min-width: 55%" v-if="refItem !== null">
        <div class="box rowBox">
          <div class="hyTitle">{{ secoundName }}</div>
          <v-btn
            color="primary"
            dark
            @click="
              createItem(
                'secound',
                secoundName,
                secoundTableKey,
                secoundApi,
                secoundTableList,
                secoundHeaders
              )
            "
          >
            New Item
          </v-btn>
          <div class="spacer"></div>
          <v-text-field
            v-model="secoundTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :search="secoundTableSearch"
            :headers="secoundHeaders"
            :items="secoundTableList"
            :items-per-page="-1"
            :item-key="secoundTableKey"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="
                  updateItem(
                    'secound',
                    secoundName,
                    secoundTableKey,
                    secoundApi,
                    secoundTableList,
                    secoundHeaders,
                    item
                  )
                "
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(secoundApi, secoundTableList, item)"
              >
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watch, computed, nextTick } from "vue";
import { getRequest } from "@/services/request.js";
import DeleteDialog from "@/dialogs/DeleteItemDialog.vue";
import CreateDialog from "@/dialogs/CreateItemDialog.vue";
import UpdateDialog from "@/dialogs/UpdatItemDialog.vue";
// import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "SelectComponent",
  components: {
    CreateDialog,
    DeleteDialog,
    UpdateDialog,
  },
  props: {
    obj: Object,
  },
  setup(props) {
    const {
      categoryOption,

      masterNewItem,
      masterName,
      masterApi,
      masterHeaders,
      secoundName,
      secoundApi,
      secoundHeaders,
    } = props.obj;

    masterHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });
    secoundHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });

    const refItem = ref(null);
    const masterTableList = ref([]);
    const secoundTableList = ref([]);

    const masterTableSearch = ref("");
    const secoundTableSearch = ref("");

    const parentRef = {};
    const parentKey = ref(masterHeaders[0].value);
    const masterTableKey = ref(masterHeaders[0].value);
    const secoundTableKey = ref(secoundHeaders[0].value);

    // set
    masterHeaders.map((item) => {
      if (item.useUpdateItem === true) {
        parentRef[item.value] = item;
      }
      if (item.useParentKey === true) {
        parentKey.value = item.value;
      }
    });

    const getSelectedList = (code) => {
      const [fileName, apiName] = secoundApi;
      getRequest(
        [fileName, apiName + "s"],
        { parent_key: code },
        (secoundList) => {
          secoundTableList.value = secoundList || [];
        }
      );
    };

    const category = ref("");
    const categories = ref(null);

    const categoryChange = (value) => {
      category.value = value;
      init();
    };

    const init = () => {
      const [fileName, apiName] = masterApi;
      if (category.value === "") {
        getRequest([fileName, apiName + "s"], null, (masterList) => {
          masterTableList.value = masterList || [];
        });
      } else {
        getRequest(
          [fileName, apiName + "s"],
          {
            filterKey: category.value,
          },
          (masterList) => {
            masterTableList.value = masterList || [];
          }
        );
      }
    };

    if (!categoryOption) {
      init();
    } else {
      const [fileName, apiName] = categoryOption.api;
      getRequest([fileName, apiName], null, (list) => {
        category.value = list[0][categoryOption.valueKey];
        categories.value = list;
        init();
      });
    }

    const selectTableItem = (item, row) => {
      refItem.value = item;
      getSelectedList(item[parentKey.value]);
      row.select(true);
    };

    //
    const createPayload = ref(null);
    const updatePayload = ref(null);
    const deletePayload = ref(null);

    const createItem = (type, title, key, api, list, fields) => {
      const parentItem = {};
      if (type === "secound") {
        for (const key in parentRef) {
          if (parentRef[key].asKey) {
            parentItem[parentRef[key].asKey] = refItem.value[key];
          } else {
            parentItem[key] = refItem.value[key];
          }
        }
      }

      parentItem[key] = null;
      parentItem.saveType = "new";

      createPayload.value = {
        title,
        api,
        list,
        fields,
        //
        parentItem,
      };
    };
    const updateItem = (type, title, key, api, list, fields, item) => {
      const parentItem = {};
      if (type === "secound") {
        for (const key in parentRef) {
          if (parentRef[key].asKey) {
            parentItem[parentRef[key].asKey] = refItem.value[key];
          } else {
            parentItem[key] = refItem.value[key];
          }
        }
      }

      parentItem[key] = item[key];
      parentItem.saveType = "update";

      updatePayload.value = {
        title,
        api,
        list,
        fields,
        item,
        //
        parentItem,
      };
    };
    const deleteItem = (api, list, item) => {
      deletePayload.value = {
        list,
        item,
        api,
      };
    };

    return {
      refItem,

      masterTableKey,
      secoundTableKey,

      categoryChange,
      categoryOption,
      category,
      categories,

      //
      masterNewItem,
      masterName,
      masterApi,
      masterHeaders,
      masterTableList,

      secoundName,
      secoundApi,
      secoundHeaders,
      secoundTableList,

      masterTableSearch,
      secoundTableSearch,

      selectTableItem,

      createItem,
      updateItem,
      deleteItem,

      createPayload,
      updatePayload,
      deletePayload,
    };
  },
};
</script>
