<template>
  <div class="component">
    <div class="box">
      <!--  -->
      <div class="col" style="max-width: 50%">
        <div class="box rowBox">
          <div class="hyTitle">내부태그</div>

          <div class="spacer"></div>
          <v-select
            v-model="ship"
            :items="ships"
            :item-text="(item) => item.shipname"
            menu-props="auto"
            label="선박"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
          >
          </v-select>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :headers="refTableHeaders"
            :items="refTableList"
            :items-per-page="-1"
            :item-key="refTableHeaders[0].value"
          >
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small @click="copyItem(item)">
                mdi-arrow-right-bold
              </v-icon>
              <!-- <v-icon small @click="editItem(item)"> mdi-pencil </v-icon> -->
              <!-- <v-icon small @click="deleteBowlTableItem(item)"> mdi-delete </v-icon> -->
            </template>
          </v-data-table>
        </div>
      </div>
      <!--  -->
      <div class="col" style="max-width: 50%">
        <div class="box rowBox">
          <div class="hyTitle">외부태그</div>
          <div class="spacer"></div>
          <v-text-field
            v-model="refTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :headers="bowlTableHeaders"
            :items="bowlTableList"
            :items-per-page="-1"
            :item-key="bowlTableHeaders[0].value"
          >
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small @click="deleteBowlTableItem(item)">
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watch, computed, nextTick } from "vue";
// import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "TransferComponent",
  components: {},
  setup() {
    //
    //
    const ships = ref([
      { shipid: "1", shipname: "아리온제주1" },
      { shipid: "2", shipname: "아리온제주2" },
      { shipid: "3", shipname: "아리온제주3" },
      { shipid: "4", shipname: "아리온제주4" },
      { shipid: "5", shipname: "아리온제주5" },
      { shipid: "5", shipname: "아리온제주6" },
      { shipid: "5", shipname: "아리온제주7" },
      { shipid: "5", shipname: "아리온제주8" },
      { shipid: "5", shipname: "아리온제주9" },
    ]);
    const ship = ref(null);

    const refTableSearch = ref("");
    const refTableList = ref([]);
    const refTableHeaders = [
      // { text: "", align: "center", value: "glutenfree" },
      {
        text: "NO",
        align: "center",
        value: "no",
      },
      { text: "선박", align: "center", value: "shipname" },
      { text: "장비ID", align: "center", value: "equipid" },
      { text: "태그", align: "center", value: "tagname" },
      { text: "Description", align: "center", value: "tagdesc" },
      { text: "Actions", value: "actions", sortable: false },
    ];

    const bowlTableList = ref([]);
    const bowlTableHeaders = ref([
      // { text: "", align: "center", value: "glutenfree" },
      { text: "Actions", value: "actions", sortable: false },
      {
        text: "NO",
        align: "center",
        value: "no",
      },
      { text: "선박", align: "center", value: "shipname" },
      { text: "장비ID", align: "center", value: "equipid" },
      { text: "태그", align: "center", value: "tagname" },
      { text: "Description", align: "center", value: "tagdesc" },
    ]);

    //
    const dialog = ref(false);
    const dialogDelete = ref(false);

    const editedIndex = ref(-1);
    const editedItem = ref({
      // no: 0,
      shipname: "",
      equipid: "",
      tagname: "",
      tagdesc: "",

      // name: "",
      // calories: 0,
      // fat: 0,
      // carbs: 0,
      // protein: 0,
    });
    const defaultItem = ref({
      // no: 0,
      shipname: "",
      equipid: "",
      tagname: "",
      tagdesc: "",
    });

    const copyItem = (item) => {
      const cloneItem = Object.assign({}, item);
      bowlTableList.value.push(cloneItem);
      // editedIndex.value = bowlTableList.value.indexOf(item);
      // editedItem.value = Object.assign({}, item);
      // dialog.value = true;
    };

    const editItem = (item) => {
      editedIndex.value = bowlTableList.value.indexOf(item);
      editedItem.value = Object.assign({}, item);
      dialog.value = true;
    };

    const deleteBowlTableItem = (item) => {
      bowlTableList.value.splice(bowlTableList.value.indexOf(item), 1);

      // editedIndex.value = bowlTableList.value.indexOf(item);
      // editedItem.value = Object.assign({}, item);
      // dialogDelete.value = true;
    };

    // const deleteItemConfirm = () => {
    //   bowlTableList.value.splice(editedIndex.value, 1);
    //   closeDelete();
    // };

    const close = () => {
      dialog.value = false;
      nextTick(() => {
        editedItem.value = Object.assign({}, defaultItem.value);
        editedIndex.value = -1;
      });
    };

    const closeDelete = () => {
      dialogDelete.value = false;
      nextTick(() => {
        editedItem.value = Object.assign({}, defaultItem.value);
        editedIndex.value = -1;
      });
    };

    const save = () => {
      if (editedIndex.value > -1) {
        Object.assign(bowlTableList.value[editedIndex.value], editedItem.value);
      } else {
        bowlTableList.value.push(editedItem.value);
      }
      close();
    };

    watch(dialog, (val) => {
      val || close();
    });

    watch(dialogDelete, (val) => {
      val || closeDelete();
    });

    const formTitle = computed(() => {
      return editedIndex.value === -1 ? "New Item" : "Edit Item";
    });

    return {
      formTitle,

      dialog,
      dialogDelete,
      bowlTableHeaders,
      bowlTableList,
      editedIndex,
      editedItem,
      defaultItem,
      copyItem,
      editItem,
      deleteBowlTableItem,
      // deleteItemConfirm,
      close,
      closeDelete,
      save,

      refTableSearch,
      //
      ships,
      ship,
      //
      refTableHeaders,
      refTableList,
    };
  },
};
</script>

<style></style>
