<template>
  <div class="component">
    <div class="box">
      <div class="col">
        <div class="box rowBox">
          <!--  -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Delete</v-card-title>
              <p class="dialogContent">해당 아이탬을 삭제 하시겠습니까?</p>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="insertDialog" max-width="1200px">
            <v-card class="full">
              <v-card-title>
                <span class="text-h6">Insert Item</span>
              </v-card-title>
              <div class="rowBox">
                <v-spacer></v-spacer>
                <v-text-field
                  v-model="insertTableSearch"
                  append-icon="mdi-magnify"
                  label="검색"
                  single-line
                  hide-details
                ></v-text-field>
              </div>
              <v-container>
                <v-data-table
                  fixed-header
                  :search="insertTableSearch"
                  :headers="insertHeaders"
                  :items="insertTableList"
                  :items-per-page="-1"
                  :item-key="insertHeaders[1].value"
                >
                  <template v-slot:[`item.actions`]="{ item }">
                    <div class="actionsBox">
                      <v-icon small @click="pushItem(item)">
                        mdi-playlist-plus
                      </v-icon>
                    </div>
                  </template>
                </v-data-table>
              </v-container>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!--  -->
          <div class="hyTitle">내부태그</div>
          <v-select
            v-if="categoryOption !== undefined"
            @change="categoryChange"
            v-model="category"
            :items="categories"
            :item-text="(item) => item[categoryOption.textKey]"
            :item-value="(item) => item[categoryOption.valueKey]"
            menu-props="auto"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
            style="width: 250px"
            return-object
          >
          </v-select>
          <div class="spacer"></div>
          <v-text-field
            v-model="masterTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            class="spliceTable"
            fixed-header
            :search="masterTableSearch"
            :headers="joinHeaders"
            :items="masterTableList"
            :items-per-page="-1"
            :item-key="masterHeaders[0].value"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <div class="actionsBox">
                <v-icon small class="mr-2" @click="getInsertItems(item)">
                  mdi-playlist-check
                </v-icon>
                <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
              </div>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ref,
  // watch,
  // nextTick,
} from "vue";
// import { useAuthStore } from "@/stories/mainStore.js";
import { getRequest, postRequest } from "@/services/request.js";

export default {
  name: "InsertComponent",
  components: {},
  props: {
    obj: Object,
  },

  setup(props) {
    const {
      mappingKey,
      insertKeys,

      categoryOption,
      saveKey,
      saveApi,
      masterApi,
      masterHeaders,
      insertHeaders,
      insertApi,
    } = props.obj;

    masterHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });

    let categoryObj = null;
    const category = ref(null);
    const categories = ref(null);
    const masterTableList = ref([]);
    const masterTableSearch = ref("");
    const joinHeaders = ref([...masterHeaders, ...insertHeaders]);

    // //
    const itemInsert = ref(null);
    const itemDelete = ref(null);

    const insertDialog = ref(false);
    const insertTableList = ref([]);
    const insertTableSearch = ref("");
    //
    const dialogDelete = ref(false);

    const init = async () => {
      const [fileName, apiName] = masterApi;
      getRequest(
        [fileName, apiName + "s"],
        {
          filterKey: category.value,
        },
        (masterList) => {
          masterTableList.value = masterList || [];
        }
      );
    };

    if (!categoryOption) {
      init();
    } else {
      const [fileName, apiName] = categoryOption.api;
      getRequest([fileName, apiName], null, (list) => {
        categoryObj = list[0];
        category.value = list[0][categoryOption.valueKey];
        categories.value = list;
        init();
      });
    }

    const categoryChange = (obj) => {
      category.value = obj[categoryOption.valueKey];
      categoryObj = obj;
      init();
    };

    const pushItem = (item) => {
      insertHeaders.map((obj) => {
        const prop = obj.value;
        if (prop !== "actions") {
          itemInsert.value[prop] = item[prop];
        }
      });

      // 개별저장
      const body = {};
      saveKey.map((key) => {
        body[key] = categoryObj[key];
      });

      const saveItem = {
        SaveType: itemInsert.value[mappingKey] === null ? "new" : "update",
      };
      insertKeys.map((key) => {
        saveItem[key] = itemInsert.value[key];
      });

      body.list = [saveItem];
      postRequest(saveApi, body, (result) => {
        // console.log(result[0]);
        close();
      });
    };

    const getInsertItems = (item) => {
      //
      insertDialog.value = true;
      //
      const qo = {};
      if (categoryOption) {
        qo.filterKey = category.value;
      }
      itemInsert.value = item;
      getRequest(insertApi, qo, (list) => {
        insertTableList.value = list;
      });
    };

    const deleteItem = (item) => {
      itemDelete.value = item;
      dialogDelete.value = true;
    };
    const deleteItemConfirm = () => {
      if (itemDelete.value[mappingKey] === null) {
        closeDelete();
        return;
      }

      const body = {};
      saveKey.map((key) => {
        body[key] = categoryObj[key];
      });
      const saveItem = {
        SaveType: "delete",
      };
      insertKeys.map((key) => {
        saveItem[key] = itemDelete.value[key];
      });

      body.list = [saveItem];
      postRequest(saveApi, body, (result) => {
        for (const key in result[0]) {
          itemDelete.value[key] = result[0][key];
        }
        closeDelete();
      });
    };
    const close = () => {
      insertDialog.value = false;
    };
    const closeDelete = () => {
      dialogDelete.value = false;
    };

    // watch(dialog, (val) => {
    //   val || close();
    // });

    // watch(dialogDelete, (val) => {
    //   val || closeDelete();
    // });

    return {
      masterHeaders,
      categoryChange,
      categoryOption,
      category,
      categories,

      masterTableSearch,
      joinHeaders,
      masterTableList,

      insertDialog,
      getInsertItems,
      insertHeaders,
      insertTableSearch,
      insertTableList,

      dialogDelete,
      pushItem,
      deleteItem,
      deleteItemConfirm,
      close,
      closeDelete,
    };
  },
};
</script>

<style></style>
